.hidden {
  opacity: 0;
}

@keyframes slide {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(10px);
  }
}

.fade-in {
  animation: 1.2s cubic-bezier(.39, .575, .565, 1) both fade-in;
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.fade-out {
  animation: 1.2s cubic-bezier(.39, .575, .565, 1) both fade-out;
}

@keyframes fade-out {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.fade-out-in {
  animation: 1.2s cubic-bezier(.39, .575, .565, 1) both fade-out-in;
}

@keyframes fade-out-in {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.slide-in-left {
  animation: .5s cubic-bezier(.25, .46, .45, .94) both slide-in-left;
}

@keyframes slide-in-left {
  0% {
    opacity: 0;
    transform: translateX(-1000px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.slide-in-right {
  animation: 1s ease-in-out both slide-in-right;
}

@keyframes slide-in-right {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0);
  }
}

.slide-in-top {
  animation: .5s cubic-bezier(.25, .46, .45, .94) both slide-in-top;
}

@keyframes slide-in-top {
  0% {
    opacity: 0;
    transform: translateY(-1000px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.slide-out-right {
  animation: 1s ease-in-out both slide-out-right;
}

@keyframes slide-out-right {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(100%);
  }
}

