  transform: translate(0, 2px);
}

.btn--primary:link .icon, .btn--primary:visited .icon {
  animation: 1.5s ease-in-out infinite slide;
}

.btn--primary:link:before, .btn--primary:visited:before {
  content: "";
  z-index: -1;
  background-color: #e2b65966;
  width: 100%;
  height: 100%;
  transition: transform .4s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(7px, 7px);
}

.btn--primary:link:hover:before, .btn--primary:link:focus:before, .btn--primary:visited:hover:before, .btn--primary:visited:focus:before {
  transform: translate(0);
}

.btn--primary-reversed:link .icon, .btn--primary-reversed:visited .icon {
  animation: 1.5s ease-in-out infinite reverse slide;
}

.btn--secondary:link, .btn--secondary:visited {
  z-index: 0;
