  position: relative;
}

.btn--secondary:link:before, .btn--secondary:visited:before {
  content: "";
  z-index: -1;
  background: #e2b65966;
  width: 100%;
  height: 100%;
  transition: transform .4s ease-in-out;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%)scaleX(0);
}

.btn--secondary:link:hover:before, .btn--secondary:link:focus:before, .btn--secondary:visited:hover:before, .btn--secondary:visited:focus:before {
  transform: translateX(-50%)scaleX(1);
}

