.timeline__list:before {
  content: "";
  background-color: #aaa;
  width: 3px;
  height: 100%;
  position: absolute;
  top: 0;
}

.timeline__list:after {
  content: "";
  -webkit-clip-path: polygon(100% 0, 0 0, 50% 100%);
  clip-path: polygon(100% 0, 0 0, 50% 100%);
  background-color: #666;
  width: 30px;
  height: 30px;
  margin-bottom: -30px;
  position: absolute;
  bottom: 15px;
}

