  width: 30px;
  height: 3px;
  position: absolute;
  top: 50%;
  left: 100%;
}

.timeline__item.role {
  width: clamp(100px, 40%, 20em);
  margin: -.7em 0;
  right: 0;
  transform: translateX(calc(-50% - 30px));
}

.timeline__item.role:before {
  left: 100%;
}

